<template>
  <div>
    <div class="wyl_retrieve">
      <van-cell-group>
        <van-field v-model="pass" placeholder="请输入密码" />
      </van-cell-group>
      <van-cell-group>
        <van-field v-model="password" placeholder="请确认密码" />
      </van-cell-group>
    </div>
    <div class="wyl_login_button">
      <button @click="onsure">确定</button>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
//引入Vant请提示
import { Toast } from "vant";
Vue.use(Toast);

export default {
  data() {
    return {
      pass: "",
      password: "",
    };
  },
  methods: {
    onsure() {
      if (this.pass == this.password) {
        this.$APP
          .change({
            mobile: this.$route.query.sms,
            password: this.password,
            sms_code: this.$route.query.sss,
            client: 1,
            type: 2,
          })
          .then((res) => {
            this.$router.push('/');
          });
      }else{
        Toast("两次密码不一致，请重新输入。");
        return;
      }
    },
  },
};
</script>

<style>
.wyl_retrieve {
  width: 100%;
  height: 25vh;
}
.wyl_login_button {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 0.8rem;
}
.wyl_login_button button {
  width: 90%;
  height: 7vh;
  background: orange;
  border-radius: 18px;
  border: none;
}
</style>